import { Observable } from 'rxjs/Rx';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/catch';
var Wq2aHttpInterceptor = /** @class */ (function () {
    function Wq2aHttpInterceptor() {
    }
    Wq2aHttpInterceptor.prototype.intercept = function (req, next) {
        console.log("intercepted request ... ");
        // Clone the request to add the new header.
        req.body["ip"] = "172.17.0.7";
        var authReq = req.clone({ headers: req.headers.set("Access-Control-Allow-Origin", "*") });
        console.log("Sending request with new header now ...");
        console.log(req);
        console.log(req.body);
        // send the newly created request
        return next.handle(authReq).catch(function (error, caught) {
            //intercept the respons error and displace it to the console
            console.log("Error Occurred");
            console.log(error);
            //return the error to the method that called it
            return Observable.throw(error);
        });
    };
    return Wq2aHttpInterceptor;
}());
export { Wq2aHttpInterceptor };



