import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import { map, catchError, retry } from 'rxjs/operators';
import { of } from 'rxjs/observable/of';

import { Msg } from './msg';

const baseapi = 'http://appapi.wq2a.com';
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type':  'application/json',
    'Authorization': 'my-auth-token'
  })
}

@Injectable()
export class RequestService {

  constructor(private http: HttpClient) { }

  httpGet(url: string): Observable<Msg> {
    return this.http.get<Msg>(baseapi + '/' + url)
      .pipe(
        map(res => {
          console.log(res);
          return res;
        }),
//        catchError(err => {
//          console.log(err);
//          return err;
//        })
      );
  }

  httpPost(url: string, data: any): Observable<Msg> {
    return this.http.post<Msg>(baseapi + '/' + url, data, httpOptions)
      .pipe(
        map(res => {
          console.log(res);
          return res;
        }),
//        catchError(err => {
//          console.log(err);
//          return err;
//        })
      );
  }

}



