import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
var baseapi = 'http://appapi.wq2a.com';
var httpOptions = {
    headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': 'my-auth-token'
    })
};
var RequestService = /** @class */ (function () {
    function RequestService(http) {
        this.http = http;
    }
    RequestService.prototype.httpGet = function (url) {
        return this.http.get(baseapi + '/' + url)
            .pipe(map(function (res) {
            console.log(res);
            return res;
        }));
    };
    RequestService.prototype.httpPost = function (url, data) {
        return this.http.post(baseapi + '/' + url, data, httpOptions)
            .pipe(map(function (res) {
            console.log(res);
            return res;
        }));
    };
    return RequestService;
}());
export { RequestService };



